import React from "react"
import { Link } from "gatsby"
import { useStaticQuery, graphql } from "gatsby"

const Category = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        totalCount
        group(field: frontmatter___main) {
          totalCount
          fieldValue
          subcategories: group(field: frontmatter___sub) {
            totalCount
            fieldValue
          }
        }
      }
    }
  `)

  const categories = data.allMarkdownRemark.group
  const totalPosts = data.allMarkdownRemark.totalCount

  return (
    <ul>
      <Link to="/">전체 보기 ({totalPosts})</Link>
      {categories.map((category, index) => (
        <div key={index}>
          <Link to={`/category/${category.fieldValue}`}>
            {category.fieldValue} ({category.totalCount})
          </Link>
          <ul>
            {category.subcategories.map((subcategory, index) => (
              <Link
                to={`/category/${category.fieldValue}/${subcategory.fieldValue}/`}
                key={index}
              >
                {subcategory.fieldValue} ({subcategory.totalCount})
              </Link>
            ))}
          </ul>
        </div>
      ))}
    </ul>
  )
}

export default Category
